<template>
  <div class='card'>
    <div class='card-img-top' src='' alt='Card image cap'>
      <img class="card-img-top" data-src="holder.js/100px180/" alt="100%x180" style="height: 180px; width: 100%; display: block;" src="data:image/svg+xml;charset=UTF-8,%3Csvg%20width%3D%22318%22%20height%3D%22180%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20viewBox%3D%220%200%20318%20180%22%20preserveAspectRatio%3D%22none%22%3E%3Cdefs%3E%3Cstyle%20type%3D%22text%2Fcss%22%3E%23holder_15edeba7df3%20text%20%7B%20fill%3Argba(255%2C255%2C255%2C.75)%3Bfont-weight%3Anormal%3Bfont-family%3AHelvetica%2C%20monospace%3Bfont-size%3A16pt%20%7D%20%3C%2Fstyle%3E%3C%2Fdefs%3E%3Cg%20id%3D%22holder_15edeba7df3%22%3E%3Crect%20width%3D%22318%22%20height%3D%22180%22%20fill%3D%22%23777%22%3E%3C%2Frect%3E%3Cg%3E%3Ctext%20x%3D%22118.0859375%22%20y%3D%2297.35%22%3E318x180%3C%2Ftext%3E%3C%2Fg%3E%3C%2Fg%3E%3C%2Fsvg%3E" data-holder-rendered="true">
    </div>
    <button class='btn btn-lg btn-primary' @click="openForm" v-show="!isCreating" id='add-more-tasks'>
      <i class='fa fa-plus'></i>
    </button>
    <div class='card-block' v-show="isCreating">
      <form>
        <div class='form-group'>
          <label>Title</label>
          <input v-model="titleText" type='text' class='form-control'>
        </div>
        <div class='form-group'>
          <label>Description</label>
          <input v-model="descriptionText" type='text' class='form-control'>
        </div>
        <div>
          <button class='btn btn-success' @click="sendForm()">
            Create
          </button>
          <button class='btn btn-danger' @click="closeForm">
            Cancel
          </button>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      titleText: '',
      descriptionText: '',
      isCreating: false
    }
  },
  methods: {
    openForm () {
      this.isCreating = true
    },
    closeForm () {
      this.isCreating = false
    },
    sendForm () {
      if (this.titleText.length > 0 && this.descriptionText.length > 0) {
        const title = this.titleText
        const description = this.descriptionText
        this.$emit('create-todo', {
          title,
          description,
          done: false
        })
        this.titleText = ''
        this.descriptionText = ''
        this.isCreating = false
      }
    }
  }
}
</script>

<style scoped>
  .card {
    position: relative;
    z-index: 2;
    border-radius: 0;
  }
  .card-block {
    padding: 25px;
    position: relative;
  }
  #add-more-tasks {
    position: absolute;
    top: 156px;
    right: 35px;
    border-radius: 2rem;
    box-shadow: 0 4px 7px 0 rgba(127, 127, 127, 0.44);
    cursor: pointer;
  }
</style>



